* {
  margin: 0;
  padding: 0;
  background-color: #f8f8ff;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-rendering: optimizeLegibility;
}

body {
  color: #101320;
  margin-bottom: 24px;
}

main {
  text-align: center;
  display: grid;
  grid-template-columns: repeat(auto-fit,minmax(300px,904px));
  grid-gap: 30px;
  padding: 12% 0 20px;
  justify-items: center;
  justify-content: center;
  align-items: start;
}

hr {
  border: none;
  height: 1px;
  color: #212121;
  background-color: #e7e7e7;
}

h2 {
  font-weight: 400;
  font-family: "Space Mono";
  font-size: 16px;
  text-transform: uppercase;
}

h3 {
  font-size: 16px;
  line-height: 40px;
  font-weight: 500;
}

.contact {
  display: grid;
  justify-content: center;
  align-content: center;
  gap: 4px;
  grid-auto-flow: column;
}

.work {
  padding-top: 18%;
  width: 100%;
  text-align: left;
}

.links {
  padding-top: 10%;
  width: 100%;
  text-align: left;
}

.title-box {
  padding-bottom: 32px;
}

header {
  padding: 30px 10%;
  text-align: center;
}

.name {
  font-size: 16px;
  line-height: 64px;
  font-family: 'Space Mono';
  text-transform: uppercase;
}

.intro {
  font-size: 56px;
  line-height: 72px;
  font-weight: 400;
  text-align: center;  
  font-family: 'Neue Haas Grotesk Display Pro', sans-serif;
}

.intro h1 {
  font-size: 56px;
  line-height: 72px;
  font-weight: 400;
}

.intro a {
  font-weight: 500;
  color: #101320;
  font-size: 56px;
  text-decoration: none;
  -webkit-transition: all .3s ease 0s;
    transition: all .3s ease 0s;
}

.item-center {
  display: grid;
  grid-auto-flow: column;
  gap: 4px;
  align-items: center;
  justify-items: center;
}

.active {
  color: #1D567D;
}

.row h1 {
  font-size: 24px;
  font-weight: 500;
  color:#101320;
  height: 60px;
}

.row p.date {
  font-size: 14px;
  line-height: 8px;
  padding-bottom: 8px;
  font-family: 'Space Mono';
  color: #fe5311;
  text-transform: uppercase;
  margin-bottom:12px;
}

.row .social {
  font-weight: 600;
  font-size: 18px!important;
  text-transform: uppercase;
  font-style: italic;
  line-height: 10px;
  font-family: 'Barlow Condensed', sans-serif;
}

.row p.desc {
  font-size: 16px;
  line-height: 24px;
  margin-bottom: 20px;
}

.row a {
  font-size: 18px;
  font-weight: 600;
  text-transform: uppercase;
  line-height: 10px;
  color: #101320;
}

.row p {
  font-family: 'Inter', sans-serif;
}

.row a::before {
  content: "→ ";
  font-size: 22px;
}

.title-box h2::after {
  content:" ↓";
  font-size: 18px;
}

.caseContainer a::before {
   content: "→ ";
   font-size: 22px;
}

.intro a:hover {
  color: #fe5311  ;
}

.presentation a:hover {
  color: #1ED760  ;
}


.text--small {
  font-size: 15px;
  font-family: 'Space Mono', monospace;
  text-transform: uppercase;
}

.smiley--small {
  font-size: 20px;
}

.caseContainer {
  padding-top: 24px;
  padding-bottom: 24px;
}

.caseContainer:last-child {
  padding-bottom: 0px;
}

.caseContainer img {
  border-radius: 40px;
  width: 100%;
  height: auto;
  margin-right: 24px;
}

.caseContainer img:last-child{
  margin-right: 0px;
}

.caseContainer p {
  font-size: 16px;
  line-height: 24px;
  margin-bottom: 16px;
  color: #101320;
  font-family: 'Inter', sans-serif;
}

.caseContainer h1 {
  font-size: 24px;
  font-weight: 600;
  color:#101320;
}

.caseDescription a {
  font-size: 16px;
  text-transform: uppercase;
  font-family: 'Barlow Condensed', sans-serif;
  font-style: italic;
  line-height: 10px;
  color: #101320;
  font-weight: 600;
}

@media (min-width: 320px) and (max-width: 480px) {
  .intro {
    font-size: 32px;
    padding: 24px;
    overflow-wrap: break-word;
  }

  p.date {
    line-height: 30px!important;
    margin-bottom: 0px!important;
  }
  
  .row a {
    font-size: 18px!important;
  }
  
  .caseDescription a {
    font-size: 18px!important;
  }
  
  .row h1 {
    padding-top: 10px;
    margin-bottom:0px;
    padding-bottom: 0px;
    font-size: 24px;
  }
  
  .links {
    padding-top: 16%;
  }
  
  .intro a {
    font-size: 36px;
  }

  .intro p {
    text-align: left;
  }

  .intro h1 {
    font-size: 34px;
    line-height: 50px;
    text-align: left;
  }

  .smiley--small {
    display: none;
  }

  .contact {
    display: none;
  }

  .caseContainer img {
    width: 100%;
    height: auto;
    margin-bottom: 16px;
  }

  .caseDescription p {
    line-height: 28px!important;
  }

  .caseDescription h1 {
    line-height: 40px;
    padding-bottom: 24px;
    padding-top: 16px;
    font-weight: 600;
  }

  .intro p {
    line-height: 56px;
  }

  .row p.date {
    padding-bottom: 0px;
  }

}